import React, { useRef, useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import * as d3 from 'd3';
import { select } from 'd3-selection';
import { scaleLinear, scaleSequential } from 'd3-scale';
import * as d3Chromatic from 'd3-scale-chromatic';
import cloud from 'd3-cloud';

// Define color scheme mapping to d3 interpolators
const COLOR_SCHEMES = {
    // Sequential schemes (single-hue)
    'Blues': d3Chromatic.interpolateBlues,
    'Greens': d3Chromatic.interpolateGreens,
    'Greys': d3Chromatic.interpolateGreys,
    'Oranges': d3Chromatic.interpolateOranges,
    'Purples': d3Chromatic.interpolatePurples,
    'Reds': d3Chromatic.interpolateReds,
    
    // Sequential schemes (multi-hue)
    'Turbo': d3Chromatic.interpolateTurbo,
    'Viridis': d3Chromatic.interpolateViridis,
    'Inferno': d3Chromatic.interpolateInferno,
    'Magma': d3Chromatic.interpolateMagma,
    'Plasma': d3Chromatic.interpolatePlasma,
    'Cividis': d3Chromatic.interpolateCividis,
    'Warm': d3Chromatic.interpolateWarm,
    'Cool': d3Chromatic.interpolateCool,
    'CubehelixDefault': d3Chromatic.interpolateCubehelixDefault,
    'BuGn': d3Chromatic.interpolateBuGn,
    'BuPu': d3Chromatic.interpolateBuPu,
    'GnBu': d3Chromatic.interpolateGnBu,
    'OrRd': d3Chromatic.interpolateOrRd,
    'PuBuGn': d3Chromatic.interpolatePuBuGn,
    'PuBu': d3Chromatic.interpolatePuBu,
    'PuRd': d3Chromatic.interpolatePuRd,
    'RdPu': d3Chromatic.interpolateRdPu,
    'YlGnBu': d3Chromatic.interpolateYlGnBu,
    'YlGn': d3Chromatic.interpolateYlGn,
    'YlOrBr': d3Chromatic.interpolateYlOrBr,
    'YlOrRd': d3Chromatic.interpolateYlOrRd,
    
    // Cyclical
    'Rainbow': d3Chromatic.interpolateRainbow,
    'Sinebow': d3Chromatic.interpolateSinebow,
    
    // Diverging
    'BrBG': d3Chromatic.interpolateBrBG,
    'PRGn': d3Chromatic.interpolatePRGn,
    'PiYG': d3Chromatic.interpolatePiYG,
    'PuOr': d3Chromatic.interpolatePuOr,
    'RdBu': d3Chromatic.interpolateRdBu,
    'RdGy': d3Chromatic.interpolateRdGy,
    'RdYlBu': d3Chromatic.interpolateRdYlBu,
    'RdYlGn': d3Chromatic.interpolateRdYlGn,
    'Spectral': d3Chromatic.interpolateSpectral,
};

/**
 * WordDash is a component that displays a cloud of words.
 * The size of each word is determined by its frequency.
 * Clicking on a word selects it, dimming all other words
 * and outlining the selected word in black.
 */
const WordDash = (props) => {
    const {
        id,
        words,
        width,
        height,
        fontSizeRange,
        fontFamily,
        padding,
        rotate,
        rotationAngles,
        colorScheme,
        customColors,
        setProps,
        selectedWord
    } = props;

    const svgRef = useRef(null);
    const [worddashData, setWordDashData] = useState([]);
    const [internalSelectedWord, setInternalSelectedWord] = useState(selectedWord);
    const [layoutGenerated, setLayoutGenerated] = useState(false);
    const [wordElements, setWordElements] = useState(null);
    const [layoutData, setLayoutData] = useState(null);

    // Process the words dictionary into a format suitable for d3-cloud
    useEffect(() => {
        if (!words || Object.keys(words).length === 0) {
            setWordDashData([]);
            return;
        }

        const entries = Object.entries(words);
        const maxCount = Math.max(...entries.map(([_, count]) => count));
        const minCount = Math.min(...entries.map(([_, count]) => count));
        
        const fontSizeScale = scaleLinear()
            .domain([minCount, maxCount])
            .range(fontSizeRange);

        const data = entries.map(([text, count]) => ({
            text,
            size: fontSizeScale(count),
            count
        }));

        setWordDashData(data);
    }, [words, fontSizeRange]);

    // Parse custom colors if provided
    const [wordColors, setWordColors] = useState({});
    useEffect(() => {
        if (customColors) {
            try {
                const colors = JSON.parse(customColors);
                setWordColors(colors);
            } catch (e) {
                console.error("Error parsing custom colors:", e);
            }
        } else {
            setWordColors({});
        }
    }, [customColors]);

    // Apply color scheme to words
    useEffect(() => {
        if (!words || Object.keys(words).length === 0) {
            return;
        }

        const entries = Object.entries(words);
        const maxCount = Math.max(...entries.map(([_, count]) => count));
        const minCount = Math.min(...entries.map(([_, count]) => count));

        // Create color scale if a color scheme is specified
        let colorScale;
        if (colorScheme && COLOR_SCHEMES[colorScheme]) {
            colorScale = scaleSequential()
                .domain([minCount, maxCount])
                .interpolator(COLOR_SCHEMES[colorScheme]);
        }

        // If we have layout data and word elements, update colors without regenerating layout
        if (layoutData && wordElements) {
            wordElements
                .style("fill", d => {
                    if (d.text === internalSelectedWord) return "#000";
                    // Use custom color if available, otherwise use color scheme
                    if (wordColors[d.text]) return wordColors[d.text];
                    return colorScale ? colorScale(d.count) : "#1f77b4";
                })
                .style("opacity", d => d.text === internalSelectedWord || !internalSelectedWord ? 1 : 0.3)
                .style("stroke", d => d.text === internalSelectedWord ? "#000" : "none")
                .style("stroke-width", d => d.text === internalSelectedWord ? 1 : 0);
        }
    }, [colorScheme, words, internalSelectedWord, layoutData, wordElements, wordColors]);

    // Generate the worddash layout once
    useEffect(() => {
        if (!svgRef.current || worddashData.length === 0 || layoutGenerated) {
            return;
        }

        const svg = select(svgRef.current);
        svg.selectAll("*").remove();

        const layout = cloud()
            .size([width, height])
            .words(worddashData)
            .padding(padding)
            .rotate(() => {
                if (!rotate) return 0;
                // Use the provided rotation angles or default to a range of angles
                const angles = rotationAngles || [-60, -30, 0, 30, 60];
                return angles[Math.floor(Math.random() * angles.length)];
            })
            .font(fontFamily)
            .fontSize(d => d.size)
            .on("end", draw);

        layout.start();

        function draw(words) {
            // Store the layout data for later use
            setLayoutData(words);

            // Create color scale if a color scheme is specified
            let colorScale;
            if (colorScheme && COLOR_SCHEMES[colorScheme]) {
                const maxCount = Math.max(...words.map(d => d.count));
                const minCount = Math.min(...words.map(d => d.count));
                colorScale = scaleSequential()
                    .domain([minCount, maxCount])
                    .interpolator(COLOR_SCHEMES[colorScheme]);
            }

            const g = svg.attr("width", width)
               .attr("height", height)
               .append("g")
               .attr("transform", `translate(${width / 2},${height / 2})`);

            const textElements = g.selectAll("text")
               .data(words)
               .enter()
               .append("text")
               .style("font-size", d => `${d.size}px`)
               .style("font-family", fontFamily)
               .style("fill", d => {
                   if (d.text === internalSelectedWord) return "#000";
                   // Use custom color if available, otherwise use color scheme
                   if (wordColors[d.text]) return wordColors[d.text];
                   return colorScale ? colorScale(d.count) : "#1f77b4";
               })
               .style("opacity", d => d.text === internalSelectedWord || !internalSelectedWord ? 1 : 0.3)
               .style("stroke", d => d.text === internalSelectedWord ? "#000" : "none")
               .style("stroke-width", d => d.text === internalSelectedWord ? 1 : 0)
               .attr("text-anchor", "middle")
               .attr("transform", d => `translate(${d.x},${d.y}) rotate(${d.rotate})`)
               .text(d => d.text)
               .on("click", (event, d) => {
                   const newSelectedWord = d.text === internalSelectedWord ? null : d.text;
                   setInternalSelectedWord(newSelectedWord);
                   
                   if (setProps) {
                       setProps({ 
                           selectedWord: newSelectedWord,
                           clickData: newSelectedWord ? {
                               word: newSelectedWord,
                               count: d.count
                           } : null
                       });
                   }
               })
               .style("cursor", "pointer");
            
            setWordElements(textElements);
            setLayoutGenerated(true);
        }
    }, [worddashData, width, height, fontFamily, padding, rotate, rotationAngles, wordColors]);

    // Reset layout when words change
    useEffect(() => {
        setLayoutGenerated(false);
        setLayoutData(null);
    }, [words]);

    // Update internal state when selectedWord prop changes
    useEffect(() => {
        setInternalSelectedWord(selectedWord);
    }, [selectedWord]);

    return (
        <div id={id} style={{ width: width, height: height }}>
            <svg ref={svgRef} width={width} height={height}></svg>
        </div>
    );
};

WordDash.defaultProps = {
    width: 600,
    height: 400,
    fontSizeRange: [10, 60],
    fontFamily: 'Arial, sans-serif',
    padding: 5,
    rotate: true,
    rotationAngles: [-60, -45, -30, -15, 0, 15, 30, 45, 60],
    colorScheme: 'Viridis',
    words: {},
    selectedWord: null
};

WordDash.propTypes = {
    /**
     * The ID used to identify this component in Dash callbacks.
     */
    id: PropTypes.string,

    /**
     * Dictionary of words and their counts.
     * Format: { word1: count1, word2: count2, ... }
     */
    words: PropTypes.objectOf(PropTypes.number),

    /**
     * Width of the worddash container in pixels.
     */
    width: PropTypes.number,

    /**
     * Height of the worddash container in pixels.
     */
    height: PropTypes.number,

    /**
     * Range of font sizes to use for the words.
     * Format: [minSize, maxSize]
     */
    fontSizeRange: PropTypes.arrayOf(PropTypes.number),

    /**
     * Font family to use for the words.
     */
    fontFamily: PropTypes.string,

    /**
     * Padding between words in pixels.
     */
    padding: PropTypes.number,

    /**
     * Whether to rotate some words.
     */
    rotate: PropTypes.bool,

    /**
     * Array of possible rotation angles in degrees.
     * Words will be randomly rotated to one of these angles.
     */
    rotationAngles: PropTypes.arrayOf(PropTypes.number),

    /**
     * Color scheme to use for the words.
     * Uses Plotly/d3 color scheme names.
     */
    colorScheme: PropTypes.string,

    /**
     * Custom colors for specific words.
     * JSON string with word as key and color as value.
     */
    customColors: PropTypes.string,

    /**
     * Currently selected word.
     */
    selectedWord: PropTypes.string,

    /**
     * Data about the clicked word.
     * Format: { word: string, count: number }
     */
    clickData: PropTypes.shape({
        word: PropTypes.string,
        count: PropTypes.number
    }),

    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func
};

export default WordDash;
